import React,{useEffect, useState} from 'react'
import Banner from '../components/Banner';
import { useParams } from 'react-router-dom';
import Sidebar from '../components/Sidebar';
function BlogDetail() {
    const [docTitle,setDocTitle] = useState("")
    const [detail,setDetail] = useState({})
    let {id} = useParams()
    
    const getPostsDetail = async(blog_id) => {
      await fetch('http://localhost:4000/api/blog/detail/'+blog_id)
      .then(response => response.json())
      .then(json => setDetail(json[0]))
    }
    useEffect(()=>{
        document.title = 'Blog Detail';
        setDocTitle(document.title)
        getPostsDetail(id)
    },[id])
  return (
    <>
    <Banner title={docTitle} />
    <section className="ftco-section ftco-degree-bg">
      <div className="container">
        <div className="row">
          <div className="col-lg-8 ftco-animate">
						<h2 className="mb-3">{detail.title}</h2>
            <p>
              <img src={detail.image} alt="" className="img-fluid" />
            </p>
            <p>{detail.description}</p>
          
          </div> 
          <Sidebar/>

        </div>
      </div>
    </section>
    </>
  )
}

export default BlogDetail