import { createSlice } from '@reduxjs/toolkit'

export const cartSlice = createSlice({
    name: 'cart',
    initialState: {
        cartArr: [],
        cartAmount: { subtotal: 0, discount: 0, delivery: 0, total: 0 }
    },
    reducers: {
        addToCart: (state, action) => {
            let existence = state.cartArr.find(arr => arr._id === action.payload._id)
            if (!existence) {
                action.payload.quantity = 1;
                state.cartArr.push(action.payload)
            }
        },
        removeFromCart: (state, action) => {
            let id = action.payload
            let existence = state.cartArr.find(arr => arr._id === id)
            if (existence) {
                const filteredPeople = state.cartArr.filter((item) => item._id !== id);
                state.cartArr = filteredPeople
            }
        },
        updateQuantity: (state, action) => {
            let id = action.payload.id
            let quantity = action.payload.quantity
            let existence = state.cartArr.find(arr => arr._id === id)
            if (existence) {
                existence.quantity = quantity
            }
        },
        emptyCart: (state) => {
            while (state.cartArr.length > 0) {
                state.cartArr.pop();
            }
            state.cartAmount = { subtotal: 0, discount: 0, delivery: 0, total: 0 }
        },
        totalPrice: (state, action) => {
            let totalAmt = 0
            let subtotal = 0;
            if (state.cartArr) {
                state.cartArr.forEach(item => {
                    totalAmt += item.price * item.quantity
                })
                subtotal = totalAmt;
                totalAmt = totalAmt - (action.payload.discount + action.payload.delivery)
            }
            state.cartAmount = { subtotal: subtotal, discount: action.payload.discount, delivery: action.payload.delivery, total: totalAmt }
        }
    }
})

export const { addToCart, removeFromCart, updateQuantity, emptyCart, totalPrice } = cartSlice.actions
export default cartSlice.reducer