import React, { useState } from 'react'
import axios from 'axios';
import Loading from './Loading';
function Subscribe() {
  const validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
  const [msg,setMsg] = useState("")
  const [loadingStatus,setLoadingStatus] = useState(false)
  const [email,setEmail] = useState("")
  const handleEmail = (e) =>{
    setEmail(e.target.value)
  }
  const callSubscriptionEmail = async(email) =>{
    setLoadingStatus(true) 
    try {
			await axios.post('http://localhost:4000/api/subscribe/insert',{
        email:email
      }).then(function (response) {
        setEmail("")
        setLoadingStatus(false) 
          setMsg(response.data.msg)
        });
		} catch (error) {
      setEmail("")
      setLoadingStatus(false)
			setMsg("Server error.")
		}
  }
  const subscribeForthisEmail = () =>{
    
    if(email===""){
      setMsg("Enter email address.")
    }
    else{
      if (email.match(validRegex)) {
        callSubscriptionEmail(email)
      }
      else{
        setMsg("Enter valid email address.")
      }
    }
  }
  const hideMsg = () =>{
    setMsg("")
  }
  return (
    <>
    <section className="ftco-section ftco-no-pt ftco-no-pb py-5 bg-light">
      <div className="container py-4">
        <div className="row d-flex justify-content-center py-5">  
            <div className="col-md-6">
                <h2 style={{fontSize:"22px"}} className="mb-0">Subcribe to our Newsletter</h2>
                <span>Get e-mail updates about our latest shops and special offers</span>
            </div>
            <div className="col-md-6 d-flex align-items-center">
            <form action="#" className="subscribe-form" style={{paddingBottom:"8px"}}>
                <div className="form-group d-flex">
                    <input type="text" onKeyUp={hideMsg} className="form-control" onChange={handleEmail} value={email} placeholder="Email address" />
                    {
                    loadingStatus===true?<Loading ptype="subscribe" />:<input type="button" onClick={subscribeForthisEmail} value="Subscribe" className="submit px-3" />
                    }
                </div> 
                <div className='subscribeMsg'>{msg}</div>
            </form>
            </div>
        </div>
      </div>
    </section>
    </>
  )
}

export default Subscribe