import React, { useEffect, useState } from 'react'
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux'
import { emptyCart } from '../features/cart';
import Banner from '../components/Banner'
import { Link, useSearchParams } from 'react-router-dom';
function Success() {
    const dispatch = useDispatch()
    const cartData = useSelector(state => state.cart).cartArr
    const searchParams = useSearchParams()[0]    
    const [docTitle,setDocTitle] = useState("")
    const [data,setdata] = useState({});
    const storeCustomerData = async(cart) =>{
        const {fname,lname,city,state,country,phone,zip, amount,addr1,addr2} = JSON.parse(localStorage.getItem("udata"))
        const arr = [];
        for(var k=0;k<cart.length;k++){
            const tPrice = cart[k].price * cart[k].quantity
            const tObj = {product_id:cart[k]._id, product_price: cart[k].price, quantity:cart[k].quantity, final_price:tPrice, hidden:false }
            arr.push(tObj)
        }
        const formData = {email: localStorage.getItem("email"), first_name: fname, last_name: lname, address_1: addr1, address_2: addr2,city:city,state:state,country:country,zipcode:zip,phone:phone,order_total:amount,stripe_payment_id:searchParams.get("payment_intent"),hidden:false,items:arr}
        try {
			await axios.post('http://localhost:4000/api/customer/insert',formData).then(function (response) {
            	dispatch(emptyCart());
			  });
		} catch (error) {
		}
    }
    useEffect(()=>{
        document.title = 'Payment successfull';
        setDocTitle(document.title)
        setdata(JSON.parse(localStorage.getItem("udata")));
        storeCustomerData(cartData)
    },[])
  return (
    <>
    <Banner title={docTitle} />
    <section className="ftco-section testimony-section" style={{paddingTop:"0px"}}>
      <div className="container">
        <div className="row justify-content-center mb-5 pb-3">
          <div className="col-md-7 heading-section ftco-animate text-center fadeInUp ftco-animated">
          	<span className="subheading"><i className="successIcon checkmark">✓</i></span>
            <h2 className="mb-4">Payment successfull</h2>
            <ul className="categories">
                <li><Link to="#" className='successAnchor'>Payment type <span>Card</span></Link></li>
                <li><Link to="#" className='successAnchor'>Mobile <span>{data.phone}</span></Link></li>
                <li><Link to="#" className='successAnchor'>Email <span>{localStorage.getItem("email")}</span></Link></li>
                <li><Link to="#" className='successAnchor'><b>Amount Paid</b> <span><b>Rs {data.amount}</b></span></Link></li>
                <li><Link to="#" className='successAnchor'>Transaction id <span>{searchParams.get("payment_intent")}</span></Link></li>
              </ul>
              <Link to="/shop"><button style={{marginTop:"34px"}}><span id="button-text">GO TO SHOP</span></button></Link>
          </div>
        </div>
      </div>
    </section>
    </>
  )
}

export default Success