import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { convertMongoDbDate } from '../components/common';
import axios from 'axios'

function Sidebar() {
    const [latestBlogs, setLatestBlogs] = useState([])
    const getLatestPosts = async() => {
        await axios.get('http://localhost:4000/api/recentblogs').then(function (response) {
            setLatestBlogs(response.data);
        })
        .catch(function (error) {
        })
        .finally(function () {
            // always executed
        });
    }
    useEffect(()=>{
        getLatestPosts()
    },[])
  return (
    <>
    <div className="col-lg-4 sidebar ftco-animate">
            <div className="sidebar-box ftco-animate">
              <h3 className="heading">Recent Blog</h3>

{
    latestBlogs.length>0?
    latestBlogs.map((arr,i)=>{
        return <div key={i} className="block-21 mb-4 d-flex">
            <Link className="blog-img mr-4" style={{backgroundImage:`url(${arr.image})`}}></Link>
            <div className="text">
                <h3 className="heading-1"><Link to={"/blog/detail/"+arr._id}>{arr.title}</Link></h3>
                <div className="meta">
                <div><Link to="#"><span className="icon-calendar"></span> {convertMongoDbDate(arr.inserted_at)}</Link></div>
                <div><Link to="#"><span className="icon-person"></span> {arr.uid.name}</Link></div>
                </div>
            </div>
        </div>
    })
    :<div>No record found!</div>
}
            </div>
          </div>
    </>
  )
}

export default Sidebar