import React,{useEffect, useState} from 'react'
import { Link } from 'react-router-dom';
import { convertMongoDbDate } from '../components/common';
import Banner from '../components/Banner';
import Sidebar from '../components/Sidebar';
function Blog() {
    const [docTitle,setDocTitle] = useState("")
    const [blogs, setBlogs] = useState([])
    const getPosts = async() => {
        await fetch('http://localhost:4000/api/blogs')
        .then(response => response.json())
        .then(json => setBlogs(json))
    }
    useEffect(()=>{
        document.title = 'Blogs';
        setDocTitle(document.title)
        getPosts()
    },[])
  return (
    <>
    <Banner title={docTitle} />
    <section className="ftco-section ftco-degree-bg">
      <div className="container">
        <div className="row">
          <div className="col-lg-8 ftco-animate">
				<div className="row">
	{
		blogs.map((data,i)=>{     
			return <div key={i} className="col-md-12 d-flex ftco-animate">
			<div className="blog-entry align-self-stretch d-md-flex">
			  <Link to="blog-single.html" className="block-20" style={{backgroundImage:`url(${data.image})`}}>
			  </Link>
			  <div className="text d-block pl-md-4">
				  <div className="meta mb-3">
				  <div><Link to="#">{convertMongoDbDate(data.inserted_at)}</Link></div>
				  <div><Link to="#">{data.uid.name}</Link></div>
				</div>
				<h3 className="heading"><Link to={"/blog/detail/"+data._id}>{data.title}</Link></h3>
				<p>{data.description.substring(0,115)}</p>
				<p><Link to={"/blog/detail/"+data._id} className="btn btn-primary py-2 px-3">Read more</Link></p>
			  </div>
			</div>
		  </div>
		})
	}
			</div>
          </div> 
          <Sidebar/>
        </div>
      </div>
    </section> 
    </>
  )
}

export default Blog