import React,{useEffect, useState} from 'react'
import axios from 'axios'
import { useSelector,useDispatch } from 'react-redux'
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import { totalPrice } from '../features/cart';
import Banner from '../components/Banner';
import Subscribe from '../components/Subscribe'
import CheckoutForm from './CheckoutForm';
import "./Checkout.css";
const stripePromise = loadStripe("pk_test_51NR31pSIfwIzuPEL2VltjxnyQ8QCE8uYL49GOzsSBbsXkkevXV50Ko0AYXkvaRspfUdkmLHp5P3SpgqNtxo6z69700rKlac2Fv");

function Checkout() {
	const [displayStripe,setDisplayStripe] = useState(false)
	const [formData,setFormData] = useState({amount:"",fname:"",lname:"",addr1:"",addr2:"",city:"",state:"",country:"",zip:"",phone:""})
	const [errData,setErrData] = useState({fnameErr:"",lnameErr:"",addr1Err:"",cityErr:"",stateErr:"",countryErr:"",zipErr:"",phoneErr:""})
    const dispatch = useDispatch()
    const totalCharge = useSelector(state => state.cart).cartAmount
    const [docTitle,setDocTitle] = useState("")
	const [clientSecret, setClientSecret] = useState("");
    useEffect(()=>{
        document.title = 'Checkout';
        setDocTitle(document.title)
        dispatch(totalPrice({discount:0,delivery:0}))
		handleToken(totalCharge.total)
		setFormData(formData => ({...formData, amount: totalCharge.total}))
    },[dispatch])
	const handleToken = async(amt)=>{
		try {
			await axios.post('http://localhost:4000/api/stripe/pay',{
				amount:amt
			}).then(function (response) {
				setClientSecret(response.data.clientSecret);
			  });
		} catch (error) {
		}
	}
	
	const appearance = {
		theme: 'stripe',
	  };
	  const options = {
		clientSecret,
		appearance
	  };
	
	const handleForm = (e) =>{
		setFormData({...formData,[e.target.name]:e.target.value})
		let nm = e.target.name+"Err";
		if(e.target.value!==""){
			setErrData({...errData, [nm]: ""})
		}
		else{
			if(nm==="fnameErr"){
				setErrData({...errData, [nm]: "First name required"})
			}
			else if(nm==="lnameErr"){
				setErrData({...errData, [nm]: "Last name required"})
			}
			else if(nm==="addr1Err"){
				setErrData({...errData, [nm]: "Address required"})
			}
			else if(nm==="cityErr"){
				setErrData({...errData, [nm]: "City required."})
			}
			else if(nm==="stateErr"){
				setErrData({...errData, [nm]: "State required."})
			}
			else if(nm==="countryErr"){
				setErrData({...errData, [nm]: "Country required."})
			}
			else if(nm==="zipErr"){
				setErrData({...errData, [nm]: "Zipcode required."})
			}
			else if(nm==="phoneErr"){
				setErrData({...errData, [nm]: "Phone required."})
			}
		}
	} 
	const fieldEmptyOrNot = () =>{
		if(formData.fname==="" || formData.lname==="" || formData.addr1==="" || formData.city==="" || formData.state==="" || formData.country==="" || formData.zip==="" || formData.phone===""){
			setDisplayStripe(false)
		}
	}
	const showStripe = () =>{
		let errStatus = false;
		if(formData.fname===""){
			setErrData(errData => ({...errData, fnameErr: "First name required"}))
			errStatus = true;
		}
		if(formData.lname===""){
			setErrData(errData => ({...errData, lnameErr:"Last name required."}))
			errStatus = true;
		}
		if(formData.addr1===""){
			setErrData(errData => ({...errData, addr1Err:"Address required."}))
			errStatus = true;
		}
		if(formData.city===""){
			setErrData(errData => ({...errData, cityErr:"City required."}))
			errStatus = true;
		}
		if(formData.state===""){
			setErrData(errData => ({...errData, stateErr:"State required."}))
			errStatus = true;
		}
		if(formData.country===""){
			setErrData(errData => ({...errData, countryErr:"Country required."}))
			errStatus = true;
		}
		if(formData.zip===""){
			setErrData(errData => ({...errData, zipErr: "Zipcode required"}))
			errStatus = true;
		}
		if(formData.phone===""){
			setErrData(errData => ({...errData, phoneErr:"Phone required."}))
			errStatus = true;
		}
		if(errStatus===false){
			localStorage.setItem("udata",JSON.stringify(formData))
			setDisplayStripe(true)
		}
	}
  return (
    <>
    <Banner title={docTitle} />
	<section className="ftco-section">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-xl-7 ftco-animate">
							<h3 className="mb-4 billing-heading">Billing Details</h3>
	          	<div className="row align-items-end">
	          		<div className="col-md-6 ht130">
	                <div className="form-group">
	                	<label htmlFor="firstname">First Name</label>
	                  <input type="text" name='fname' id='fname' onKeyUp={fieldEmptyOrNot} onChange={handleForm} value={formData.fname} className={errData.fnameErr===""?"form-control":"form-control errBorder"} placeholder="Enter first name" />
					  <span className="errmsg">{errData.fnameErr}</span>
	                </div>
	              </div>
	              <div className="col-md-6 ht130">
	                <div className="form-group">
	                	<label htmlFor="lastname">Last Name</label>
	                  <input type="text" name='lname' id='lname' onKeyUp={fieldEmptyOrNot} onChange={handleForm} value={formData.lname} className={errData.lnameErr===""?"form-control":"form-control errBorder"} placeholder="Enter last name" />
					  <span className="errmsg">{errData.lnameErr}</span>
	                </div>
                </div>
                	<div className="w-100"></div>
		            <div className="col-md-6 ht130">
		            	<div className="form-group">
	                	<label htmlFor="streetaddress">Address</label>
	                  	<input type="text" name='addr1' id='addr1' onKeyUp={fieldEmptyOrNot} onChange={handleForm} value={formData.addr1} className={errData.addr1Err===""?"form-control":"form-control errBorder"} placeholder="Enter address" />
					  <span className="errmsg">{errData.addr1Err}</span>
	                </div>
		            </div>
		            <div className="col-md-6 ht130 pt35">
		            	<div className="form-group">
						<input type="text" name='addr2' id='addr2' onChange={handleForm} value={formData.addr2} className="form-control" placeholder="Enter address" />
					</div>
		            </div>
		            <div className="w-100"></div>
		            <div className="col-md-6 ht130">
		            	<div className="form-group">
	                	<label htmlFor="towncity">City</label>
	                  <input type="text" name='city' id='city' onKeyUp={fieldEmptyOrNot} onChange={handleForm} value={formData.city} className={errData.cityErr===""?"form-control":"form-control errBorder"} placeholder="Enter city" />
					  <span className="errmsg">{errData.cityErr}</span>
	                </div>
		            </div>
		            <div className="col-md-6 ht130">
		            	<div className="form-group">
		            		<label htmlFor="postcodezip">State</label>
	                  <input type="text" name='state' id='state' onKeyUp={fieldEmptyOrNot} onChange={handleForm} value={formData.state} className={errData.stateErr===""?"form-control":"form-control errBorder"} placeholder="Enter state" />
					  <span className="errmsg">{errData.stateErr}</span>
	                </div>
		            </div>
					<div className="w-100"></div>
		            <div className="col-md-6 ht130">
		            	<div className="form-group">
	                	<label htmlFor="towncity">Country</label>
	                  <input type="text" name='country' id='country' onKeyUp={fieldEmptyOrNot} onChange={handleForm} value={formData.country} className={errData.countryErr===""?"form-control":"form-control errBorder"} placeholder="Enter country" />
					  <span className="errmsg">{errData.countryErr}</span>
	                </div>
		            </div>
		            <div className="col-md-6 ht130">
		            	<div className="form-group">
		            		<label htmlFor="postcodezip">Zipcode</label>
	                  <input type="text" name='zip' id='zip' onKeyUp={fieldEmptyOrNot} onChange={handleForm} value={formData.zip} className={errData.zipErr===""?"form-control":"form-control errBorder"} placeholder="Enter zipcode" />
					  <span className="errmsg">{errData.zipErr}</span>
	                </div>
		            </div>
		            <div className="w-100"></div>
		            <div className="col-md-6 ht130">
	                <div className="form-group">
	                	<label htmlFor="phone">Phone</label>
	                  <input type="text" name='phone' id='phone' onKeyUp={fieldEmptyOrNot} onChange={handleForm} value={formData.phone} className={errData.phoneErr===""?"form-control":"form-control errBorder"} placeholder="Enter phone" />
					  <span className="errmsg">{errData.phoneErr}</span>
	                </div>
	              </div>
				  {
					displayStripe===false?
					<div className="col-md-6 ht130">
		            	<div className="form-group">
							<button onClick={showStripe} style={{marginTop:"34px"}}><span id="button-text">Submit</span></button>
	                	</div>
		            </div>:""
				  }
				<div className="w-100"></div>
                </div>
	          
					</div>
					<div className="col-xl-5">
	          <div className="row mt-5 pt-3">
	          	<div className="col-md-12 d-flex mb-5">
	          		<div className="cart-detail cart-total p-3 p-md-4">
	          			<h3 className="billing-heading mb-4">Cart Total</h3>
	          			<p className="d-flex">
		    						<span>Subtotal</span>
		    						<span>Rs {totalCharge.subtotal}</span>
		    					</p>
		    					<p className="d-flex">
		    						<span>Delivery</span>
		    						<span>Rs {totalCharge.delivery}</span>
		    					</p>
		    					<p className="d-flex">
		    						<span>Discount</span>
		    						<span>Rs {totalCharge.discount}</span>
		    					</p>
		    					<hr />
		    					<p className="d-flex total-price">
		    						<span>Total</span>
		    						<span>Rs {totalCharge.total}</span>
		    					</p>
								</div>
	          	</div>
				{
					displayStripe===true?
					<div className="col-md-12">
	          		<div className="cart-detail p-3 p-md-4">
	          			<h3 className="billing-heading mb-4">Pay with stripe</h3>
						  {clientSecret && (
							<Elements options={options} stripe={stripePromise}>
							<CheckoutForm />
							</Elements>
						)}
					</div>
	          	</div>:""
				}
	          	
	          </div>
          </div>
        </div>
      </div>
    </section>
    <Subscribe/>
    </>
  )
}

export default Checkout