import React, { useEffect, useState } from 'react'
import axios from 'axios';
import { Link } from 'react-router-dom';
import Banner from '../components/Banner';
import Loading from '../components/Loading';
function Contact() {
    const [err,setErr] = useState("")
    const [formData,setFormData] = useState({name:"",email:"",subject:"",msg:""})
    const [loadingStatus,setLoadingStatus] = useState(false)
    const [docTitle,setDocTitle] = useState("")
    useEffect(()=>{
        document.title = 'Contact Us';
        setDocTitle(document.title)
    },[])
    const submitData=()=>{
      setErr("")
      let errStatus = false;
      if(formData.name===""){
        setErr("Name is required.")
        errStatus = true;
      }
      else if(formData.email===""){
        setErr("Email is required.")
        errStatus = true;
      }
      else if(formData.subject===""){
        setErr("Subject is required.")
        errStatus = true;
      }
      else if(formData.msg===""){
        setErr("Message is required.")
        errStatus = true;
      }
      if(errStatus===false){
        sendEmail(formData.name,formData.email,formData.subject,formData.msg)
      }
    }
    const sendEmail = async(name,email,subject,msg) =>{
      setLoadingStatus(true)
        try {
          await axios.post('http://localhost:4000/api/send/email',{
            email:email,name:name,subject:subject,msg:msg
          }).then(function (response) {
              setLoadingStatus(false)  
              setFormData({name:"",email:"",subject:"",msg:""})
              setErr(response.data.msg)
            });
        } catch (error) {
          setLoadingStatus(false)  
          setErr("Server error.")
        }
    }
    const handleFormData = (e)=>{
      setFormData({...formData,[e.target.name]:e.target.value})
    }
  return (
    <>
    <Banner title={docTitle} />
    <section className="ftco-section contact-section bg-light">
      <div className="container">
      	<div className="row d-flex mb-5 contact-info">
          <div className="w-100"></div>
          <div className="col-md-3 d-flex">
          	<div className="info bg-white p-4">
	            <p><span>Address:</span> #8210/9, Street no: 11/4, Gurpal nagar, Ludhiana</p>
	          </div>
          </div>
          <div className="col-md-3 d-flex">
          	<div className="info bg-white p-4">
	            <p><span>Phone:</span> <Link to="tel://917973883707">+91 7973883707</Link></p>
	          </div>
          </div>
          <div className="col-md-3 d-flex">
          	<div className="info bg-white p-4">
	            <p><span>Email:</span> <Link to="mailto:sunnypatial1985@gmail.com">sunnypatial1985@gmail.com</Link></p>
	          </div>
          </div>
          <div className="col-md-3 d-flex">
          	<div className="info bg-white p-4">
	            <p><span>Website</span> <Link to="#">sunny.com</Link></p>
	          </div>
          </div>
        </div>
        <div className="row block-9">
          <div className="col-md-12 order-md-last d-flex">
            <form action="#" className="bg-white p-5 contact-form">
              <div className="form-group">
                <input type="text" className="form-control" name="name" onChange={handleFormData} value={formData.name} placeholder="Your Name" />
              </div>
              <div className="form-group">
                <input type="text" className="form-control" name="email" onChange={handleFormData} value={formData.email} placeholder="Your Email" />
              </div>
              <div className="form-group">
                <input type="text" className="form-control" name="subject" onChange={handleFormData} value={formData.subject} placeholder="Subject" />
              </div>
              <div className="form-group">
                <textarea cols="30" rows="7" name="msg" onChange={handleFormData} value={formData.msg} className="form-control" placeholder="Message"></textarea>
              </div>
              <div className="form-group">
                {
                    loadingStatus===true?<Loading/>:<input type="button" onClick={submitData} value="Send Message" className="btn btn-primary py-3 px-5" style={{marginTop:"12px",float:"left"}} />
                }
              </div>
              <div style={{float:"left",height:"50px",marginTop:"28px",marginLeft:"20px",color:"red"}}>{err}</div>
            </form>
          </div>  
        </div>
      </div>
    </section>
    </>
  )
}

export default Contact