import React,{useEffect, useState} from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { removeFromCart,updateQuantity,totalPrice } from '../features/cart';
import { Link } from 'react-router-dom';

import Banner from '../components/Banner';
import Subscribe from '../components/Subscribe';
function Cart() {
    const [cart,setCart] = useState([]) 
    const dispatch = useDispatch()
    const cartData = useSelector(state => state.cart).cartArr
    const totalCharge = useSelector(state => state.cart).cartAmount
    const [docTitle,setDocTitle] = useState("")
    useEffect(()=>{
        document.title = 'Cart';
        setDocTitle(document.title)
        if(cartData.length>0){
          setCart(cartData)
        }
        dispatch(totalPrice({discount:0,delivery:0}))
    },[cartData,dispatch])
    
    const updateNewQuantity = (e,index) =>{
      let newArr = [...cart];
      newArr[index] = { ...newArr[index], quantity:e.target.value }
      setCart(newArr);
      if(e.target.value>0 && e.target.value!==""){
        dispatch(updateQuantity({id:newArr[index]._id,quantity:e.target.value}))
      }
    }
  return (
    <>
    <Banner title={docTitle} />
    <section className="ftco-section ftco-cart">
			<div className="container">
				<div className="row">
    			<div className="col-md-12 ftco-animate">
    				<div className="cart-list">
	    				<table className="table">
						    <thead className="thead-primary">
						      <tr className="text-center">
						        <th>Remove</th>
						        <th>Product Image</th>
						        <th>Product name</th>
						        <th>Price</th>
						        <th>Quantity</th>
						        <th>Total</th>
						      </tr>
						    </thead>
						    <tbody>
                  {
                    cart.length>0?
                    cart.map((data,i)=>{
                return <tr className="text-center" key={i}>
                        <td className="product-remove"><Link to="#" onClick={() => dispatch(removeFromCart(data._id))}><span className="ion-ios-close"></span></Link></td>
                        <td className="image-prod"><div className="img" style={{backgroundImage:`url(http://localhost:4000/images/${data.image})`}}></div></td>
                        <td className="product-name">
                          <h3>{data.title}</h3>
                          <p>{data.description}</p>
                        </td>                        
                        <td className="price">Rs {data.price}</td>
                        <td className="quantity">
                          <div className="input-group mb-3">
                            <input type="text" name={"quantity_"+i} style={{width:"50px",textAlign:"center",margin:"auto"}} className="quantity input-number" value={data.quantity} onChange={(e)=>updateNewQuantity(e,i)} min="1" max="100" />
                          </div>
                        </td>    
                        <td className="total">Rs {data.quantity*data.price}</td>
                      </tr>
                    })
                    :<tr>
                      <td colSpan="6">Empty Cart!</td>
                    </tr>
                  }
						    </tbody>
						  </table>
					  </div>
    			</div>
    		</div>
        {
          cartData.length>0?
        <div className="row justify-content-end">
    			<div className="col-lg-4 mt-5 cart-wrap ftco-animate">
    				<div className="cart-total mb-3">
    					<h3>Cart Totals</h3>
    					<p className="d-flex">
    						<span>Subtotal</span>
    						<span>Rs {totalCharge.subtotal}</span>
    					</p>
    					<p className="d-flex">
    						<span>Delivery</span>
    						<span>Rs {totalCharge.delivery}</span>
    					</p>
    					<hr />
    					<p className="d-flex total-price">
    						<span>Total</span>
    						<span>Rs {totalCharge.total}</span>
    					</p>
    				</div>
    				<p><Link to="/cart/checkout" className="btn btn-primary py-3 px-4">Proceed to Checkout</Link></p>
    			</div>
    		</div>:""
        }
    	</div>
		</section>
    <Subscribe/>
    </>
  )
}

export default Cart