import React,{useEffect, useState} from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { addToCart,removeFromCart } from '../features/cart';
import axios from 'axios';
import Banner from '../components/Banner';
import Subscribe from '../components/Subscribe';
import { Link, useParams } from 'react-router-dom';
function Shop() {
	let {id} = useParams();
	const dispatch = useDispatch()
	const cartData = useSelector(state => state.cart).cartArr
	const [docTitle,setDocTitle] = useState("")
	const [products,setProducts] = useState([])
	const [categories,setCategories] = useState([])
	const [categorieID,setcategorieID] = useState("all")
	const fetchCategories = async()=>{
		await axios.get('http://localhost:4000/api/categories').then(function (response) {
			setCategories(response.data.categories);
			})
			.catch(function (error) {
			})
			.finally(function () {
				// always executed
			});
	}
	const fetchProducts = async(catId="all")=>{
		setcategorieID(catId)
		if(catId!=="all"){
			await axios.get(`http://localhost:4000/api/products/${catId}`).then(function (response) {
            	setProducts(response.data.data);
			})
			.catch(function (error) {
			})
			.finally(function () {
				// always executed
			});
		}
		else{
			await axios.get('http://localhost:4000/api/products').then(function (response) {
            	setProducts(response.data.data);
			})
			.catch(function (error) {
			})
			.finally(function () {
				// always executed
			});
		}
	}
    useEffect(()=>{
        document.title = 'Shop';
        setDocTitle(document.title)
		localStorage.removeItem("udata")
		localStorage.removeItem("email")
		fetchCategories()
		if(id!==undefined){
			fetchProducts(id)
		}
		else{
			fetchProducts("all")
		}
    },[])
	const showByCategory = (id) =>{
		fetchProducts(id)
	}
	const isWhatPercentOf = (x, y) => {
		return parseInt((x / y) * 100);
	  }
  return (
    <>
    <Banner title={docTitle} />
    <section className="ftco-section">
    	<div className="container">
    		<div className="row justify-content-center">
    			<div className="col-md-10 mb-5 text-center">
    				<ul className="product-category">
    					<li><Link to="#" className={categorieID==="all"?"active":""} onClick={()=>showByCategory("all")}>All</Link></li>
						{
							categories.length>0?
							categories.map((dt,i)=>{
								return <li key={i}><Link className={categorieID===dt._id?"active":""} to="" onClick={()=>	showByCategory(dt._id)}>{dt.title}</Link></li>
							})
							:""
						}
    				</ul>
    			</div>
    		</div>
    		<div className="row">

				{
					products.length>0?
					products.map((data,i)=>{
						return <div key={i} className="col-md-6 col-lg-3 ftco-animate">
						<div className="product">
							<Link to="#" className="img-prod"><img className="img-fluid" src={`http://localhost:4000/images/${data.image}`} alt="Colorlib Template" />
								{data.disc_price>0?<span className="status">{isWhatPercentOf(data.disc_price-data.price,data.disc_price)}%</span>:""}
								<div className="overlay"></div>
							</Link>
							<div className="text py-3 pb-4 px-3 text-center">
								<h3><Link to="#">{data.title}</Link></h3>
								<div className="d-flex">
									<div className="pricing">
										<p className="price">
										<span className="mr-2 price-dc">{data.disc_price>0?"Rs "+data.disc_price:""}</span>
											<span className="price-sale">Rs {data.price}</span></p>
									</div>
								</div>
								<div className="bottom-area d-flex px-3">
									<div className="m-auto d-flex">
										
										{
											cartData.find(arr=>arr._id===data._id)?
											<Link to="#" onClick={() => dispatch(removeFromCart(data._id))} className="buy-now d-flex justify-content-center align-items-center mx-1">
												<span><i className="ion-ios-remove"></i></span>
											</Link>
											:
											<Link to="#" onClick={() => dispatch(addToCart(data))} className="buy-now d-flex justify-content-center align-items-center mx-1">
											<span><i className="ion-ios-cart"></i></span>
											</Link>											
										}
									</div>
								</div>
							</div>
						</div>
					</div>
					}):""
				}
    		</div>
    		<div className="row mt-5">
          {/* <div className="col text-center">
            <div className="block-27">
              <ul>
                <li><Link to="#">&lt;</Link></li>
                <li className="active"><span>1</span></li>
                <li><Link to="#">2</Link></li>
                <li><Link to="#">3</Link></li>
                <li><Link to="#">4</Link></li>
                <li><Link to="#">5</Link></li>
                <li><Link to="#">&gt;</Link></li>
              </ul>
            </div>
          </div> */}
        </div>
    	</div>
    </section>
    <Subscribe/>
    </>
  )
}

export default Shop