import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { addToCart,removeFromCart } from '../features/cart';
import axios from 'axios'
import { Link } from 'react-router-dom'
function FeatureProduct() {
	const dispatch = useDispatch()
	const cartData = useSelector(state => state.cart).cartArr
	const [featureProducts, setFeatureProducts] = useState([])
    const getFeatureProducts = async() => {
        await axios.get('http://localhost:4000/api/products/feature').then(function (response) {
            setFeatureProducts(response.data.data);
        })
        .catch(function (error) {
        })
        .finally(function () {
            // always executed
        });
    }
	const isWhatPercentOf = (x, y) => {
		return parseInt((x / y) * 100);
	  }
	useEffect(()=>{
		getFeatureProducts()
	},[])  
  return (
    <>
    <section className="ftco-section">
    	<div className="container">
				<div className="row justify-content-center mb-3 pb-3">
          <div className="col-md-12 heading-section text-center ftco-animate">
          	<span className="subheading">Featured Products</span>
            <h2 className="mb-4">Our Products</h2>
            <p>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia</p>
          </div>
        </div>   		
    	</div>
    	<div className="container">
    		<div className="row">
			{
					featureProducts.length>0?
					featureProducts.map((data,i)=>{
						return <div key={i} className="col-md-6 col-lg-3 ftco-animate">
						<div className="product">
							<Link to="#" className="img-prod"><img className="img-fluid" src={`http://localhost:4000/images/${data.image}`} alt="Colorlib Template" />
								{data.disc_price>0?<span className="status">{isWhatPercentOf(data.disc_price-data.price,data.disc_price)}%</span>:""}
								<div className="overlay"></div>
							</Link>
							<div className="text py-3 pb-4 px-3 text-center">
								<h3><Link to="#">{data.title}</Link></h3>
								<div className="d-flex">
									<div className="pricing">
										<p className="price">
										<span className="mr-2 price-dc">{data.disc_price>0?"Rs "+data.disc_price:""}</span>
											<span className="price-sale">Rs {data.price}</span></p>
									</div>
								</div>
								<div className="bottom-area d-flex px-3">
									<div className="m-auto d-flex">
										
										{
											cartData.find(arr=>arr._id===data._id)?
											<Link to="#" onClick={() => dispatch(removeFromCart(data._id))} className="buy-now d-flex justify-content-center align-items-center mx-1">
												<span><i className="ion-ios-remove"></i></span>
											</Link>
											:
											<Link to="#" onClick={() => dispatch(addToCart(data))} className="buy-now d-flex justify-content-center align-items-center mx-1">
											<span><i className="ion-ios-cart"></i></span>
											</Link>											
										}
									</div>
								</div>
							</div>
						</div>
					</div>
					}):""
				}
    			
    		</div>
    	</div>
    </section>
    </>
  )
}

export default FeatureProduct